import { AggregateEvent } from './aggregate-event';
import { AggregateId } from '../../aggregate-id';

export abstract class Aggregate {

	private readonly aggregateId: AggregateId;

	private readonly events: Array<AggregateEvent>;

	protected constructor(aggregateId: AggregateId,
						  private readonly type: string) {
		this.aggregateId = aggregateId;
		this.events = [];
	}

	getId(): AggregateId {
		return this.aggregateId;
	}

	getEvents(): ReadonlyArray<AggregateEvent> {
		return this.events as ReadonlyArray<AggregateEvent>;
	}

	addEvent(event: AggregateEvent): void;
	addEvent(events: Array<AggregateEvent>): void;
	addEvent(args: AggregateEvent | Array<AggregateEvent>): void {

		if (Array.isArray(args)) {
			for (let event of args) {
				this.events.push(event);
			}
		} else {
			this.events.push(args as AggregateEvent);
		}
	}

	clearEvents(): void {
		this.events.length = 0;
	}
}
