import { AggregateId } from '../../../domain/aggregate-id';
import { ReadModel } from '../../../domain/read/read-model';
import { ReadModelStore } from '../../../domain/read/store/read-model.store';

import { PersistAnemia } from '../persist-anemia';
import { PersistStateStore } from '../persist-state.store';
import { Optional } from '../../../common/optional';


export abstract class PersistReadModelStore<R extends ReadModel, A extends PersistAnemia> extends ReadModelStore<R> {

	protected constructor(private readonly stateStore: PersistStateStore<A>) {
		super();
	}

	abstract toReadModel(anemia: A): R;

	getById(aggregateId: AggregateId): Optional<R> {
		return this.getValue(aggregateId);
	}

	getAll(): Array<R> {
		return this.getAllValues();
	}

	private getAllValues(): Array<R> {
		return this.stateStore
				   .getAll()
				   .map((anemia: A) => this.toReadModel(anemia));
	}

	private getValue(aggregateId: AggregateId): Optional<R> {
		const optAnemia = this.stateStore.get(aggregateId);

		return optAnemia.map(this.toReadModel);
	}

}
