import { AggregateId } from '../../aggregate-id';

export abstract class AggregateEvent {

	protected constructor(
		private readonly aggregateId: AggregateId,
		private readonly type: string) {
	}

	getAggregateId(): AggregateId {
		return this.aggregateId;
	}

	getType(): string {
		return this.type;
	}

	equals(event: AggregateEvent): boolean {
		return this.equalsByType(event) &&
			this.getAggregateId().equals(event.getAggregateId());
	}

	equalsByType(event: AggregateEvent): boolean {
		return this.getType() === event.getType();
	}

}
