import { AggregateId } from '../../../domain/aggregate-id';

import { Aggregate } from '../../../domain/command/aggregate/aggregate';
import { AggregateStore } from '../../../domain/command/store/aggregate.store';
import { AggregateStoreRegister } from '../../../domain/command/store/aggregate-store.register';

import { PersistAnemia } from '../persist-anemia';
import { PersistStateStore } from '../persist-state.store';
import { Optional } from '../../../common/optional';


export abstract class PersistAggregateStore<D extends Aggregate, A extends PersistAnemia> extends AggregateStore<D> {

	protected constructor(private readonly stateStore: PersistStateStore<A>,
						  private readonly aggregateStoreRegister: AggregateStoreRegister) {
		super();
		this.aggregateStoreRegister.register(this);
	}

	abstract toAnemia(aggregate: D): A;

	abstract fromAnemia(anemia: A): D;

	save(aggregate: D): void {
		this.saveValue(aggregate);
	}

	getById(aggregateId: AggregateId): Optional<D> {
		return this.getValue(aggregateId);
	}

	private saveValue(aggregate: D): void {

		const anemia = this.toAnemia(aggregate);

		this.stateStore.set(anemia);
	}

	private getValue(aggregateId: AggregateId): Optional<D> {
		const optAnemia = this.stateStore.get(aggregateId);

		return optAnemia.map(this.fromAnemia);
	}

}
