import { Aggregate } from '../aggregate/aggregate';
import { Command } from '../command';
import { AggregateCommandHandler } from './aggregate-command.handler';
import { AggregateFactoryArchive, AggregateRepositoryArchive } from '../config/define';
import { AggregateFactory } from './aggregate.factory';
import { AggregateRepository } from '../aggregate/aggregate-repository';


export function aggregateCommandHandlerFactory<A extends Aggregate, C extends Command>(createAggregateCommandHandler: AggregateCommandHandler<A, C>, factoryArchive: AggregateFactoryArchive, aggregateRepositoryArchive: AggregateRepositoryArchive, aggregateName: string): AggregateCommandHandlerImpl<A, C> {
	return new AggregateCommandHandlerImpl<A, C>(createAggregateCommandHandler, factoryArchive, aggregateRepositoryArchive, aggregateName);
}

export class AggregateCommandHandlerImpl<A extends Aggregate, C extends Command> {

	private readonly commandType: string;

	constructor(private readonly createAggregateCommandHandler: AggregateCommandHandler<A, C>,
				private readonly aggregateFactoryArchive: AggregateFactoryArchive,
				private readonly aggregateRepositoryArchive: AggregateRepositoryArchive,
				private readonly aggregateType: string) {
		this.commandType = this.createCommandInstance().getMessageType();
	}

	publishDomainEvents(aggregate: A, command: C): void {
		this.createAggregateCommandHandler.publishDomainEvents(aggregate, command);
	}

	handleCommand(command: C) {

		const aggregateId = command.getAggregateId();

		const optFactory = this.aggregateFactoryArchive.get(this.aggregateType);

		optFactory.ifPresent((factory: AggregateFactory<A>) => {

			const aggregate = factory.create(aggregateId);

			const optRepository = this.aggregateRepositoryArchive.get(this.aggregateType);

			optRepository.ifPresent((repo: AggregateRepository<A>) => {

				repo.save(aggregate);

				this.publishDomainEvents(aggregate, command);
			});
		});

	}

	forCommand(command: C): boolean {
		return this.commandType === command.getMessageType();
	}

	private createCommandInstance(): C {
		const args: Array<void> = [],
			argumentLength = this.createAggregateCommandHandler.forCommand().constructor.length;

		args.fill(undefined, 0, argumentLength);

		return (new (this.createAggregateCommandHandler.forCommand() as any)(...args));
	}

}
