import { AggregateId } from '../../domain/aggregate-id';

export abstract class PersistAnemia {

	private readonly aggregateId: AggregateId;

	protected constructor(uid: AggregateId) {
		this.aggregateId = uid;
	}

	getAggregateId(): AggregateId {
		return this.aggregateId;
	}

	getId(): string {
		return this.getAggregateId().toString();
	}

}
