import { StaticProvider, Type } from '@angular/core';

import { DOMAIN_EVENT_HANDLERS } from './event/domain-event-handlers';
import { DomainEventHandler } from './event/domain-event.handler';

export function provideEventHandlers(handlers: Array<Type<DomainEventHandler>>): Array<StaticProvider> {
	return handlers.map((handler: Type<DomainEventHandler>) => {
		return {
			provide: DOMAIN_EVENT_HANDLERS,
			useClass: handler,
			multi: true
		};
	}) as any;
}
