import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

import { DomainEvent } from './domain-event';
import { DomainEventStore } from './domain-event.store';


@Injectable()
export class DomainEventStream extends Subject<DomainEvent> {

	constructor(private eventStore: DomainEventStore) {
		super();
	}

	next(event: DomainEvent): void {
		super.next(event);
		this.eventStore.next(event);
	}
}
