import { Inject, Injector, ModuleWithProviders, NgModule, OnDestroy, Optional, Provider, Type } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
/**
 * Command
 */
import { Aggregate } from '../domain/command/aggregate/aggregate';
import { Command } from '../domain/command/command';
import { CommandBus } from '../domain/command/command.bus';
import { CommandDispatcher } from '../domain/command/command.dispatcher';
import { CommandLogger } from '../domain/command/command.logger';
import { CommandStream } from '../domain/command/command.stream';
import { FILTERED_COMMAND_STREAM } from '../domain/command/filtered-command-stream';
import { AggregateStoreRegister } from '../domain/command/store/aggregate-store.register';
import { DomainEvent } from '../domain/event/domain-event';
import { DOMAIN_EVENT_HANDLERS } from '../domain/event/domain-event-handlers';
import { DomainEventBus } from '../domain/event/domain-event.bus';
import { DomainEventPublisher } from '../domain/event/domain-event.publisher';
import { DomainEventHandler } from '../domain/event/domain-event.handler';
import { DomainEventLogger } from '../domain/event/domain-event.logger';
import { DomainEventStore } from '../domain/event/domain-event.store';
import { DomainEventStream } from '../domain/event/domain-event.stream';
/**
 * APP
 */
import { HermesModuleConfig } from './hermes-module-config';
import { COMMAND_LOGGER_ENABLED, EVENT_LOGGER_ENABLED } from '../domain/hermes-tokens';
import { HermesApi } from '../ui-api/hermes-api';
/**
 * Infrastructure
 */
import { ConsoleCommandLogger } from '../infrastructure/logger/command/console.command.logger';
import { NoopCommandLogger } from '../infrastructure/logger/command/noop.command.logger';
import { ConsoleEventLogger } from '../infrastructure/logger/event/console.event.logger';
import { NoopEventLogger } from '../infrastructure/logger/event/noop.event.logger';
import { RandomStringGenerator } from '../common/random-string.generator';
import { HermesLoggersInitializer } from './hermes.loggers.initializer';
import { aggregateCommandHandlerFactory, AggregateCommandHandlerImpl } from '../domain/command/create-handler/aggregate-command-handler.impl';
import { AGGREGATE_COMMAND_HANDLERS } from '../domain/command/create-handler/aggregate-command-handlers.token';
import { AggregateFactoryArchive, AggregateRepositoryArchive } from '../domain/command/config/define';
import { AggregateFactory } from '../domain/command/create-handler/aggregate.factory';
import { aggregateDefinitionToken } from '../domain/command/config/aggregate-definition.token';
import { AggregateDefinition } from '../domain/command/config/aggregate-definition';
import { AggregateRepository } from '../domain/command/aggregate/aggregate-repository';
import { AggregateCommandHandler } from '../domain/command/create-handler/aggregate-command.handler';
import { HermesDomainModule } from './hermes.domain.module';
import { COMMAND_HANDLERS } from '../domain/command/handler/command-handlers';
import { commandHandlerFactory, CommandHandlerImpl } from '../domain/command/handler/command-handler-impl';
import { CommandHandler } from '../domain/command/handler/command.handler';


const hermesProviders = [
	RandomStringGenerator,
	{ provide: FILTERED_COMMAND_STREAM, useExisting: CommandStream },
	CommandBus,
	CommandStream,
	CommandDispatcher,
	AggregateStoreRegister,

	DomainEventBus,
	DomainEventStream,
	DomainEventPublisher,
	DomainEventStore,

	HermesApi
];

const providers = [
	{ provide: EVENT_LOGGER_ENABLED, useValue: true },
	{ provide: COMMAND_LOGGER_ENABLED, useValue: true },
	{ provide: CommandLogger, useFactory: commandLoggerFactory, deps: [COMMAND_LOGGER_ENABLED, ConsoleCommandLogger, NoopCommandLogger] },
	{ provide: DomainEventLogger, useFactory: eventLoggerFactory, deps: [EVENT_LOGGER_ENABLED, ConsoleEventLogger, NoopEventLogger] },
	ConsoleCommandLogger,
	NoopCommandLogger,
	NoopEventLogger,
	ConsoleEventLogger,
	HermesLoggersInitializer,
	AggregateFactoryArchive,
	AggregateRepositoryArchive,
	...hermesProviders
];

export function commandLoggerFactory(enabled: boolean, consoleCommandLogger: ConsoleCommandLogger, noopCommandLogger: NoopCommandLogger) {

	if (enabled) {
		return consoleCommandLogger;
	} else {
		return noopCommandLogger;
	}
}

export function eventLoggerFactory(enabled: boolean, consoleEventLogger: ConsoleEventLogger, noopEventLogger: NoopEventLogger) {

	if (enabled) {
		return consoleEventLogger;
	} else {
		return noopEventLogger;
	}
}

@NgModule({
	imports: [
		CommonModule
	],
	providers: providers
})
export class HermesModule implements OnDestroy {

	private readonly unsubscribe$ = new Subject();

	static defineAggregate<A extends Aggregate, C extends Command>(aggregateKey: string,
																   factory: Type<AggregateFactory<A>>,
																   repository: Type<AggregateRepository<A>>,
																   createHandler: Type<AggregateCommandHandler<A, C>>,
																   handlers: Array<Provider>): ModuleWithProviders {
		return {
			ngModule: HermesDomainModule,
			providers: [{
				provide: aggregateDefinitionToken,
				multi: true,
				useValue: {
					key: aggregateKey,
					factory: factory,
					repository: repository
				}
			}, {
				provide: aggregateKey,
				useValue: aggregateKey
			},
				factory,
				repository,
				...HermesModule.registerCreateCommandHandler(createHandler, aggregateKey),
				...handlers
			]
		};
	}

	static withConfig(config: HermesModuleConfig = { loggers: false }): ModuleWithProviders {
		return {
			ngModule: HermesModule,
			providers: providers
		};
	}

	static registerCommandHandler<A extends Aggregate, C extends Command>(
		commandHandlerType: Type<CommandHandler<A, C>>,
		aggregateName: string): Array<Provider> {

		return [
			{
				provide: commandHandlerType,
				useClass: commandHandlerType
			}, {
				provide: COMMAND_HANDLERS,
				useFactory: commandHandlerFactory,
				multi: true,
				deps: [
					commandHandlerType,
					AggregateRepositoryArchive,
					aggregateName
				]
			}];
	}

	private static registerCreateCommandHandler<A extends Aggregate, C extends Command>(
		createCommandHandlerType: Type<AggregateCommandHandler<A, C>>,
		aggregateName: string): Array<Provider> {

		return [
			{
				provide: createCommandHandlerType,
				useClass: createCommandHandlerType
			}, {
				provide: AGGREGATE_COMMAND_HANDLERS,
				useFactory: aggregateCommandHandlerFactory,
				multi: true,
				deps: [
					createCommandHandlerType,
					AggregateFactoryArchive,
					AggregateRepositoryArchive,
					aggregateName
				]
			}
		];
	}

	constructor(@Optional() @Inject(DOMAIN_EVENT_HANDLERS) eventHandlers: Array<DomainEventHandler>,
				@Optional() @Inject(AGGREGATE_COMMAND_HANDLERS) aggregateCommandHandlers: Array<AggregateCommandHandlerImpl<Aggregate, Command>>,
				@Optional() @Inject(COMMAND_HANDLERS) handlers: Array<CommandHandlerImpl<Aggregate, Command>>,
				@Optional() @Inject(aggregateDefinitionToken) definedAggregate: Array<AggregateDefinition>,
				injector: Injector,
				aggregateFactoryArchive: AggregateFactoryArchive,
				aggregateRepositoryArchive: AggregateRepositoryArchive,
				commandBus: CommandBus,
				domainEventBus: DomainEventBus,
				private hermesLoggersInitializer: HermesLoggersInitializer,
				private hermesApi: HermesApi) { // HermesApi initialization

		this.hermesLoggersInitializer.start();

		this.checkNullCommand(commandBus, handlers, aggregateCommandHandlers);
		this.checkCommandHandlerIsCollection(handlers);

		if (definedAggregate) {
			definedAggregate.forEach((def: AggregateDefinition) => {

				const factory = injector.get(def.factory),
					repository = injector.get(def.repository);

				aggregateFactoryArchive.add(def.key, factory);
				aggregateRepositoryArchive.add(def.key, repository);
			});
		}

		if (aggregateCommandHandlers) {

			aggregateCommandHandlers.forEach((handler: AggregateCommandHandlerImpl<Aggregate, Command>) => {
				commandBus
					.ofCreateAggregateHandler(handler)
					.pipe(takeUntil(this.unsubscribe$))
					.subscribe((command: Command) => {
						handler.handleCommand(command);
					});
			});
		}

		if (handlers) {

			handlers.forEach((handler: CommandHandlerImpl<Aggregate, Command>) => {
				commandBus
					.ofCommandHandler(handler)
					.pipe(takeUntil(this.unsubscribe$))
					.subscribe((command: Command) => {
						handler.handleCommand(command);
					});
			});
		}

		if (eventHandlers) {
			domainEventBus
				.pipe(takeUntil(this.unsubscribe$))
				.subscribe((event: DomainEvent) => {

					eventHandlers.forEach((handler: DomainEventHandler) => {
						handler.handle(event);
					});
				});
		}
	}

	ngOnDestroy() {
		this.unsubscribe$.next();
		this.unsubscribe$.complete();

		this.hermesLoggersInitializer.stop();
	}

	private checkNullCommand(commandBus: CommandBus, commandHandlers: Array<CommandHandlerImpl<Aggregate, Command>>, aggregateCommandHandlers: Array<AggregateCommandHandlerImpl<Aggregate, Command>>): void {
		commandBus
			.ofNullHandler(commandHandlers, aggregateCommandHandlers)
			.pipe(takeUntil(this.unsubscribe$))
			.subscribe((command: Command) => {
				console.log(`Command ${command.toString()} was not intercepted by any CommandHandler.`);
			});
	}

	private checkCommandHandlerIsCollection(commandHandlers: Array<CommandHandlerImpl<Aggregate, Command>>): void {
		if (commandHandlers && !Array.isArray(commandHandlers)) {
			console.log(`You might provided commandHandler without specifying "multi: true".`);
		}
	}

}
