import { DomainEvent } from '../../domain/event/domain-event';
import { DomainEventBus } from '../../domain/event/domain-event.bus';

import { Reactive } from '../../common/reactive';

export abstract class ReadModelRepository extends Reactive {

	protected constructor(domainEventBus: DomainEventBus) {
		super();
		domainEventBus
			.ofEvent(
				...this.forEvents()
			)
			.pipe(
				this.takeUntil()
			)
			.subscribe((event: DomainEvent) => {

				try {
					this.subscribe(event);
				} catch (e) {
					console.error(e);
				}
			});
	}

	protected abstract forEvents(): Array<typeof DomainEvent>;

	protected abstract subscribe(event: DomainEvent): void;

}
