import { Aggregate } from '../aggregate/aggregate';
import { AggregateStore } from './aggregate.store';
import { AggregateId } from '../../aggregate-id';

export class AggregateStoreRegister {

	private readonly stores: Array<any> = [];

	register(store: AggregateStore<Aggregate>): void {
		this.stores.push(store);
	}

	captureAggregatesSnapshot(aggregateId: AggregateId): { [key: string]: Aggregate } {

		if (!aggregateId) {
			return {};
		}

		let aggregates = {};

		this.stores.forEach((store: AggregateStore<Aggregate>) => {

			const aggregate = store.getById(aggregateId);

			if (aggregate) {

				let aggregateName = aggregate.constructor.name;

				aggregates[aggregateName] = aggregate;
			}

		});

		return this.cloneAggregates(aggregates);
	}

	private cloneAggregates(aggregates: { [key: string]: Aggregate }): { [key: string]: Aggregate } {
		return JSON.parse(JSON.stringify(aggregates));
	}
}
