import { AggregateId } from '../../../domain/aggregate-id';
import { Aggregate } from '../../../domain/command/aggregate/aggregate';

import { ReadModel } from '../../../domain/read/read-model';

import { ReadModelStore } from '../../../domain/read/store/read-model.store';

import { InMemoryStore } from '../in-memory.store';
import { Optional } from '../../../common/optional';

export abstract class InMemoryReadModelStore<R extends ReadModel, D extends Aggregate> extends ReadModelStore<R> {

	protected constructor(private readonly inMemoryStore: InMemoryStore<D>) {
		super();
	}

	abstract toReadModel(aggregate: D): R;

	getById(aggregateId: AggregateId): Optional<R> {
		return this.getValue(aggregateId);
	}

	getAll(): ReadonlyArray<R> {
		return this.inMemoryStore
				   .getAll()
				   .map((aggregate: D) => this.toReadModel(aggregate));
	}

	private getValue(aggregateId: AggregateId): Optional<R> {
		const optAggregate: Optional<D> = this.inMemoryStore.get(aggregateId);

		return optAggregate.map(this.toReadModel.bind(this));
	}

}
